import axios from '../../axiosServer'
// import FileSaver from 'file-saver';
import { put } from 'redux-saga/effects'
import * as actions from '../actions/index'

export function* uploadImageSaga(action){
    let header = {
        headers: {
            Authorization: 'Bearer ' + localStorage.getItem('token')
        }
    } 
    yield put({type:'startUploading'})
    let data = action.data
    try{
        const res = yield axios().post('/upload/avatar',data,header)
        yield put(actions.uploadImageSuccess(res.data))
    }
    catch(error){
        console.log(error)
        yield put(actions.uploadImageFail(error))
    }
}

export function* getProfileSaga(action){
    let header = {
        headers: {
            Authorization: 'Bearer ' + localStorage.getItem('token')
        }
    }
    try{
        const res = yield axios().get('/profile/'+localStorage.getItem('userId'),header)
        if(Object.keys(res.data).length>0){ 
            yield put(actions.getProfileSucc(res.data))
        }
    }
    catch(error){
        console.log(error)
    }
}

